body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.pink {
  background: rgb(248, 174, 186);
}
.header{
  background-image: url('/img/header-1.jpg');
  background-size: cover ;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  /* height: 80vh; */
  min-height: 30rem;
}
.dulces{
  margin-bottom: 150px;
  padding: 30px;
  width: 1000px;  
}
.dulces p {
  font-size: 50px;
  margin-top: 80px;
  background: rgba(227, 228, 228, .4);
  padding: 25px;
  border-radius: 15px; 
}

footer {
  width: 100%;
  padding: 5px;
  background: #4b63b3;
  border-top: 2px solid #fff;
}
footer p {
    color: #fff;
    font-size: 18px; 
}

    
.formulario input[type="text"],
.formulario textarea {
      border:1px solid #cecece;
      border-radius: 5px;
      padding: 10px 15px;
      font-size: 18px;
      color:#a4a4a4;
}
    
.formulario input[type="text"]:focus,
.formulario textarea:focus {
      border:2px solid #3895ea;
    }
    
.formulario input[type="text"] {
      width: 100%;
      margin-bottom: 15px;
    }
    
.formulario textarea {
      width: 100%;
      height: 100px;
      min-width: 100%;
      max-width: 100%;
      min-height:100px;
      max-height: 300px;
      margin-bottom: 15px;
    }
    
.formulario .boton {
      margin: auto;
      background: #3895ea;
      color: #fff;
      border: none;
      border-radius: 3px;
      padding: 15px 40px;
      cursor: pointer;
    }
    
.formulario .boton:hover {
      background: #387bea;
    }
.titulo-recetas{
  font-size: 25px;
  background: #f2f2f2;
}
.div-receta {
  cursor: pointer;
}
.div-receta:hover {
  border: 2px solid #f2f2f2;
  border-radius: 10px; 
}
.img-receta{
  border-radius: 50%;
  max-width: 100%;
  min-width: 100%;
}
button{
  cursor: pointer;
}
a{
  color: #000;
  text-decoration: none;
}
a:hover{
  color: #000;
  text-decoration: none;
}
.img-completa{
  max-width: 100%;
  min-width: 100%;
}
.preparacion{
  background: #f2f2f2;
}
.navegacion {
  background: rgba(227, 228, 228, .4);
  border-radius: 5px;
}
.enlace{
  font-size: 20px;
  border: 2px solid transparent;
  color: #000;
}
.enlace:hover{
  font-size: 22px;
  text-decoration: #387bea;
  border: none;
  color: #000;
}
.nav-receta{
  height: 250px;
}
@media only screen and (max-width: 767px ){
  .nav-receta{
    height: 165px;
  }
}
/* =========Spinner============== */

.spinner{
  margin: 100px auto 50px;
  width: 70px;
  text-align: center;
}
.spinner > div {
  width: 15px;
  height: 15px;
  background: #5cb85c;
  margin-right: 2px;

  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}
.spinner .bounce1 {
-webkit-animation-delay: -0.32s;
animation-delay: -0.16s;
}
.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
  }
@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0)
  }
  40% {
    -webkit-transform: scale(1.0)
  }

}
@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0)
  }
  40% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }

}